.note {
  position: relative;
  width: 100%;
  max-width: 900px;
  border: 1px solid var(--main-color);
  border-radius: 4px;
  margin: 5px auto;

  .tag-label {
    position: absolute;
    top: 0;
    right: 0;
    color: black;
    font-weight: 600;
    background: var(--main-color);
    padding: 2.5px;
    border-bottom-left-radius: 3px;
  }

  .note_contetnt {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    width: 100%;
    height: 100%;
  
      .note-item_title {
        text-align: center;
        overflow: hidden;
        word-break: break-word;
        text-decoration: underline;
        padding: 1px 3px;
        width: 87.7%;
      }
      .note-item_description {
        display: flex;
        flex-wrap: wrap;
        text-align: justify;
        overflow: hidden;
        word-break: break-word;
        padding: 2.5px 5px;
        width: 100%;
        min-height: 100px;
      }
      .todo-btn-block {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
        width: 100%; 
        height: 30px;
      }
  }
}

.hilight {
  background-color: var(--main-color);
  color: black;
  height: 1rem;
}
